.recipe-btn {
    margin: 8px;
    background: -webkit-gradient( linear, left top, left bottom, from(white), color-stop(white), color-stop(white), to(grey));
    background: -webkit-linear-gradient( white, white, white, grey);
    background: -o-linear-gradient( white, white, white, grey);
    background: linear-gradient( white, white, white, grey);
    color: rgb(0, 0, 0);
    text-align: center;
}

.recipe-btn:hover {
background: -webkit-gradient( linear, left top, left bottom, from(grey), color-stop(white), color-stop(white), to(white));
background: -webkit-linear-gradient( grey, white, white, white);
background: -o-linear-gradient( grey, white, white, white);
background: linear-gradient( grey, white, white, white);
}


