.brew-cont {
    color: white;
    background: -webkit-gradient( linear,left top, left bottom,from(grey),color-stop(black), to(black));
    background: -webkit-linear-gradient( grey,black, black);
    background: -o-linear-gradient( grey,black, black);
    background: linear-gradient( grey,black, black);
}

.h2-brew {
    margin-top: 10px;
    margin-bottom: 18px;
    color: white;
    font-family: 'Gothic A1', sans-serif;
    margin-left: 12px;
}


